.Order {
  width: 100%;
  height: 30px;
}
.Label {
  width: 165px;
  font-size: 1.1em;
  font-weight: bold;
  color: #444;
  opacity: 0.7;
  float: left;
}
.Count {
  width: calc(100% - 165px);
  float: right;
  font-size: 1.2em;
  font-weight: bold;
  color: #2874ff;
  overflow: hidden;
}