.Item {
  width: 220px;
  height: 240px;
  background-color: #fff;
  border: 1px solid #ccc;
  display: inline-block;
  margin: 8px;
  border-radius: 4px;
  cursor: pointer;
}
.Item:hover {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}
.Photo {
  height: 80%;
  width: 95%;
  margin: auto;
  border: 1px solid #ccc;
  background-color: white;
  border-radius: 4px;
  position: relative;
  text-align: center;
  margin-top: 7px;
}
.Img {
  width: 100%;
  height: 100%;
  border-radius: 4px;
}
.OSI {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: #ccc;
  opacity: 0.7;
  font-size: 3em;
  display: flex;
  justify-content:center;
  align-content:center;
  flex-direction:column;
}
.Name {
  box-sizing: border-box;
  margin: auto;
  width: 95%;
  height: 20%;
  text-align: center;
  font-weight: bold;
  color: #2874ff;
  overflow: hidden;
  position: relative;
  top: 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow-wrap: break-word;
  hyphens: auto;
}